footer {
  background-color: #0d1117; /* Dark background */
}
  
footer h4, footer h5 {
  color: #ffffff; /* White text for headings */
}
  
footer ul {
  padding: 0;
}
  
footer ul li {
  color: #cccccc; /* Light gray for list items */
  line-height: 2;
  font-size: 18px;
}
  
footer ul li:hover {
  color: #ffffff; /* Change color on hover */
  cursor: pointer;
}
  
footer p {
  font-size: 16px;
  color: #bbbbbb; /* Subtle text */
}
  
footer .btn-success {
  background-color: #198754;
  border: none;
}
  
footer .btn-success:hover {
  background-color: #155c25;
}
  